/*
 *  Account
 */

.c-account {
  background-color: $s-color--background;
  padding: 2.5rem $s-gutter--edge-mobile;
  padding-bottom: 3rem;
  max-width: $s-page-wrap--xlarge;
  margin: 0 auto;

  p {
    font-size: 1rem;
  }

  &__input {
    padding-right: 5.625rem;
  }

  &__container {
    width: 100%;
    max-width: 100rem;
    margin: 0 auto;
  }

  &__main {
    background-color: $s-color--background;
  }

  &__detail {
    margin-bottom: 1.25rem;

    p {
      margin: 0.5rem 0;
    }

    &-title {
      display: block;
      font-weight: bold;
      margin-bottom: 0.5rem;
    }
  }

  &__details {
    &-title {
      margin-top: 0;
    }
  }

  &__orders {
    &-container {
      overflow: auto;

      table {
        border-spacing: 0 0;
      }

      th, td {
        white-space: nowrap;
        vertical-align: baseline;
        text-align: left;
        padding-right: 1.5rem;
        padding-bottom: 0.5rem;
      }

      a {
        @include t-font--link;
        position: relative;
        padding-bottom: 0.0625rem;
        cursor: pointer;
        color: $s-color--font-focus;

        &::before {
          position: absolute;
          left: 0;
          right: 0;
          bottom: 0;
          height: 0.0625rem;
          background: $s-color--border-focus;
          content: '';
        }
      }
    }
  }

  &__sidebar{
    padding-bottom: 1rem;
  }

  @include t-media-query($s-small-up) {
    padding: 4rem $s-gutter--edge-medium;
    padding-bottom: 3rem;

    &__main {
      display: grid;
      grid-template-columns: min(50%, 25rem) max(50%, calc(100% - 27rem));
      gap: 1rem;

      .c-page--order &, .c-page--addresses & {
        display: block;
      }
    }

    &__details-inner{
      display: block;
    }

    &__orders-table {
      width: 100%;
    }

    &__input {
      padding: 0.5rem 4.375rem 0.5rem 1.25rem;
      font-size: 0.625rem;
      letter-spacing: 0.05em;
    }
  }

  @include t-media-query($s-large-up) {
    padding: 8rem $s-gutter--edge-large;
  }

  @include t-media-query($s-large-up) {
    &__input {
      font-size: 0.875rem;
      line-height: 1.45;
      letter-spacing: 0.04357em;
      padding: 0.9rem 1.25rem;
    }
  }
}
