/*
 *  Login Register
 *
 *  Version:
 *    1.0.0 - 2020/11/04
 */

.c-login-register {
  margin: 2.5rem auto;
  display: flex;
  flex-direction: column;

  &__login-toggle,
  &__register-toggle {
    display: none;
  }

  &__login-toggle.is-visible,
  &__register-toggle.is-visible {
    display: block;
    color:$s-color--font-link;
    text-align: center;
    margin-top: 1rem;
    margin-bottom: 2rem;
    padding: 0 $s-gutter--edge-mobile;
  }

  &__form-container.is-register {
    display: none;

    &.is-visible{
      display:block;
    }
  }

  &__form-container.is-login {
    display: none;

    &.is-visible{
      display:block;
    }
  }


  .o-btn:not(.is-guest) {
    margin-top: 1rem;
  }

  .errors {
    text-align: left;
    color: $s-color--error;

    & > ul {
      padding-left: 1.25rem;
      list-style: none;
    }
  }

  .o-form__input-group {
    margin-bottom: 1rem;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  @include t-media-query($s-small-up) {
    flex-direction: row;

    &__login-toggle.is-visible,
    &__register-toggle.is-visible {
      display: none;
    }

    &__form-container.is-register {
      display: block;
      width:50%;
    }

    &__form-container.is-login {
      display: block;
      width:50%;
    }
  }

  @include t-media-query($s-medium-up) {
    padding: 0 3rem;
    flex-direction: row;
    flex-wrap: wrap;

    &__errors {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    &__form {
      max-width: 80%;

      &-container {
        &.is-error-form {
          width: 100%;
        }

        &.is-login,
        &.is-register {
          width: 50%;
        }
      }
    }
  }

  @include t-media-query($s-large-up) {
    margin: 9rem auto;
    padding: 0 10rem;
    max-width: 100rem;

    &__input {
      font-size: 0.875rem;
      line-height: 1.45;
      letter-spacing: 0.04357em;
      padding: 0.9rem 1.25rem;
    }
  }
}
