/*
 *  Activate Account
 */


.c-activate-account {
  position: relative;
  overflow: visible;
  max-width: 36rem;
  margin: 2.5rem auto 1rem;
  padding: 3rem $s-gutter--edge-mobile;
  text-align: center;

  .errors {
    text-align: left;
    color: $s-color--error;

    & > ul {
      margin-top: 0;
    }
  }

  &__input {
    border-radius: 1.5625rem;
    padding-right: 5.625rem;
  }

  &__row {
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
  }

  &__btn {
    &.is-left {
      margin-bottom: 0.5rem;
    }
  }

  .o-form__input-group {
    margin-bottom: 1rem;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  @include t-media-query($s-small-up) {
    padding: 3rem 0;

    &__row {
      flex-direction: row;
      margin-top: 1rem;
      justify-content: center;
    }

    &__btn {
      &.is-left {
        margin-right: 0.5rem;
      }
      &.is-right {
        margin-left: 0.5rem;
      }
    }
  }

  @include t-media-query($s-medium-up) {
    padding: 5rem 0;

    &__input {
      padding: 0.5rem 4.375rem 0.5rem 1.25rem;
      font-size: 0.625rem;
      letter-spacing: 0.05em;
    }
  }

  @include t-media-query($s-large-up) {
    padding: 10rem 0;

    &__input {
      font-size: 0.875rem;
      line-height: 1.45;
      letter-spacing: 0.04357em;
      padding: 0.9rem 1.25rem;
    }

    &__row {
      margin-top: 2rem;
    }
  }
}
