/*
 * Order
 */

.c-order {
  &__order-table {
    width: 100%;

    th, td {
      white-space: nowrap;
      vertical-align: baseline;
      text-align: left;
      padding-right: 1.5rem;
      padding-bottom: 0.5rem;
    }

    &-container {
      overflow-x: auto;
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-small-up) {
    &__order-detail-row {
      display: grid;
      grid-template-columns: repeat(2, 50%);
      margin-bottom: 2rem;
    }
  }
}
