/*
 *  Account Navigation
 */

.c-account-navigation {
  &__list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: grid;
    grid-auto-flow: column;
    width: max-content;
    grid-gap: 1rem;

    &-item {
      background-color: $s-color--background;
      margin-bottom: 0.625rem;
      position: relative;
      transition: 0.2s all;
      color:$s-color--font-link;

      &.current, &:hover {
        transition: 0.2s all;
      }
    }
  }

  &__link {
    display: block;
    height: 100%;
    width: max-content;
      border-bottom: 1px solid transparent;

    &:hover{
      border-bottom: 1px solid currentColor;
    }
  }

}
