.c-addresses {
  &__form {
    display: none;

    &.is-visible {
      display: block;
    }

    .o-form__input-group {
      margin: .625rem 0;

      &.is-dropdown {
        position: relative;

        &::after {
          content: "";
          display: block;
          width: 0.75rem;
          height: 0.75rem;
          background: url('./../../assets/icon-chevron-down.svg') no-repeat center center;
          right: 1.25rem;
          pointer-events: none;
          @include t-absolute-center-y();
        }
      }
    }
  }

  &__btn {
    margin: 1rem 1rem 0 0;
  }

  &__address-title {
    margin-top: 2rem;
  }


  /*** Media Queries ***/

  @include t-media-query($s-small-up) {
    &__addresses {
      display: grid;
      grid-template-columns: repeat(2, 50%);
    }
  }
}
