.c-register-form {
  padding: 0 $s-gutter--edge-mobile 3.75rem;

  &__error {
    display: none;
    color: $s-color--border-error;

    &.is-visible {
      display: block;
    }
  }

  &__date-container {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
    width: 100%;

    .o-input {
      color: rgba($s-color--font, 0.5);
    }

    .o-select {
      background-color: $s-color--background;
      padding: 1rem 2.5rem 1rem 1.25rem;
      border: 0.0625rem solid $s-color--border-focus;
      border-radius: 0.25rem;
      outline: none;
      width: 100%;
      color: $s-color--font;
      opacity: 0.9;

      &:focus {
        border: 0.0625rem solid $s-color--border-focus;
        border-radius: 0.25rem;
      }
    }

    .o-form__group {
      position: relative;
      &.day::after,
      &.month::after,
      &.year::after {
        content: "";
        display: block;
        position: absolute;
        height: 0.875rem;
        width: 0.875rem;
        top: 50%;
        right: 1.25rem;
        background-image: url("./../../assets/icon-arrow-down.svg");
        background-repeat: no-repeat;
        background-position: center;
        transform: translateY(-50%);
        pointer-events: none;
        z-index: 5;
      }
    }
  }

  &__birthday-label {
    display: block;
    margin-bottom: 0.5rem;
  }

  @include t-media-query($s-medium-up) {
    &__date-container {
      grid-template-columns: 2fr 3fr 3fr;
    }
  }
}
