/*
 * Reset Password Template
 */

.c-reset-password-template {
  position: relative;
  overflow: visible;
  max-width: 36rem;
  margin: 2.5rem auto 1rem;
  padding: 3rem $s-gutter--edge-mobile;
  text-align: center;

  .errors {
    text-align: left;
    color: $s-color--error;

    & > ul {
      margin-top: 0;
    }
  }

  @include t-media-query($s-small-up) {
    padding: 3rem 0;
  }

  @include t-media-query($s-medium-up) {
    padding: 5rem 0;
  }

  @include t-media-query($s-large-up) {
    padding: 10rem 0;
  }
}
